* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  color: #333;
  font-size: 16px; 
}

.navbar {
  background-color: #0f0e0e;
  padding: 20px 0;
  text-align: center;
}

.navbar ul {
  display: flex;
  justify-content: center;
  list-style: none;
}

.navbar li {
  margin: 0 15px;
}

.menu-link {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.menu-link:hover {
  transform: scale(1.1);
  background-color: #00ffea;
}

/* Estilos para as páginas */
.page-container {
  padding: 15px;
  max-width: 600px;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.page-container h2 {
  font-size: 20px;
  margin-bottom: 15px;
  background-color: #333;
  color: white;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
}

/* Campos de entrada de texto */
.input-field {
  width: 45%;
  padding: 8px;
  margin: 10px 0;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 14px;
  background-color: #fafafa;
}

.input-field:focus {
  border-color: #007BFF;
  outline: none;
  background-color: #e6f2ff;
}

/* Botões pequenos */
button {
  width: 10%;
  padding: 10px;
  background-color: #178a17;
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

/* Mensagens de confirmação */
.success-message {
  width: 25%;
  padding: 12px;
  background-color: #28a745;
  color: white;
  border-radius: 10px;
  margin-top: 15px;
  font-size: 20px;
  border: 2px solid #28a745;
  animation: slideIn 0.5s ease-in-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Caixa de borda em volta das informações */
.page-container form {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  border: 2px solid #ddd;
  margin-bottom: 15px;
}

/* Organizando os campos e informações na página */
.page-container form p {
  font-size: 20px;
  margin-bottom: 10px;
}

/* Organizando o campo de texto com borda */
.success-message {
  width: 45%;
  padding: 30px;
  background-color: #28a745;
  color: white;
  border-radius: 4px;
  margin-top: 15px;
  font-size: 16px;
  border: 2px solid #28a745;
  animation: slideIn 0.5s ease-in-out;
}

.page-container p {
  text-align: center;
  margin-top: 15px;
}

.page-container a {
  color: #007BFF;
  text-decoration: none;
  font-size: 14px;
}

.page-container a:hover {
  text-decoration: underline;
}
.campaign-form {
  width: 100%;
  margin-bottom: 15px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.campaign-form input,
.campaign-form textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 14px;
  background-color: #f8f8f8;
}

.campaign-form button {
  padding: 8px;
  background-color: #28a745;
  border: none;
  color: white;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.campaign-form button:hover {
  background-color: #218838;
}

.campaign-form p {
  text-align: center;
  font-size: 14px;
}

.evaluate-campaign {
  width: 100%;
  margin-bottom: 15px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.evaluate-campaign input,
.evaluate-campaign textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 14px;
  background-color: #f8f8f8;
}

.evaluate-campaign button {
  padding: 8px;
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.evaluate-campaign button:hover {
  background-color: #0056b3;
}

.evaluate-campaign .success-message {
  width: 25%;
  padding: 15px;
  background-color: #28a745;
  color: white;
  border-radius: 4px;
  margin-top: 15px;
  font-size: 16px;
  border: 2px solid #28a745;
  animation: slideIn 0.5s ease-in-out;
}

/* Organizando os campos e informações na avaliação e cadastro de campanha */
.campaign-form,
.evaluate-campaign {
  display: flex;
  flex-direction: column;
  gap: 30px; /* Menor espaçamento entre os campos */
  margin-bottom: 20px;
}

.campaign-form p,
.evaluate-campaign p {
  font-size: 14px;
  margin-bottom: 5px;
}
